import React from 'react';
import {Link, NavLink, Route} from "react-router-dom";
import s from '../year-services/YearService.module.scss';
import back from '../../../img/back_arrow.svg';
import {services} from '../knowledgeData';
import {Info} from "../year-services/Info";
import SlideRoutes from "react-slide-routes";
import {useLocation} from "react-router";

export const OnceServices = () => {

    const location = useLocation();
    const base = '/knowledge-base/once-services/';

    return <div className={s.wrapper}>
        <Link to={'/knowledge-base'}><img src={back} alt={'back'}/><h6>Назад</h6></Link>
        <div className={s.header}>
            <NavLink activeClassName={s.active} to={base + 'SmartCard'}><h3>SmartCard</h3></NavLink>
            <NavLink activeClassName={s.active} to={base + 'Multisoft'}><h3>Multisoft</h3></NavLink>
            <NavLink activeClassName={s.active} to={base + 'OfficeBundle'}><h3>OfficeBundle</h3></NavLink>
            <NavLink activeClassName={s.active} to={base + 'TV'}><h3>TV</h3></NavLink>
        </div>
        <SlideRoutes location={location}>
            {services[1].os.map(
                (os, i) => <Route
                    key={i}
                    path={base + os.name}
                    render={() => <Info service={os.inner}/>}
                />
            )}
        </SlideRoutes>
    </div>
}